export default function stringProduct(amount) {
  if (Number(amount.toString()
    .slice(-1)) === 1) {
    return '';
  }
  if (Number(amount.toString() >= 11 && Number(amount.toString() <= 20))) {
    return '';
  } if (Number(amount.toString()
    .slice(-1)) >= 2 && Number(amount.toString()
    .slice(-1)) <= 4) {
    return '';
  }
  return '';
}
